import * as React from 'react';

import { LocaleContext } from '../../shared/context';
import Translate, { translate } from '../common/Translate';

import { lines, specialWords } from './data';

import './error.scss';


const Error = (props: { activeIndex: number }) => {
    const { locale, setLocale } = React.useContext(LocaleContext);

    let localeLines = [...lines];
    [ 'badrequest', 'notfound', 'append' ].forEach((selector) => {
        for (let i = 0; i < localeLines.length; i++) {
            localeLines[i] = localeLines[i].replace(selector, translate(`error:${selector}`, locale))
        }
    });

    return (
        <div id="error">
            <h1><Translate selector="error:breakpoint"/></h1>
            <ul className="container has-shadow">
                {multLine(localeLines, props.activeIndex)}
            </ul>
        </div>
    );
}

function styleLine(line: string): JSX.Element {
    if (line === '') return null;

    for (let key in specialWords) {
        if (key !== 'string') {
            for (let word of specialWords[key]) {
                let index = line.indexOf(word);
                if (index !== -1) {
                    line = line.replace(word, '');
                    return (
                        <>
                            {styleLine(line.substring(0, index))}
                            <span className={key}>{word}</span>
                            {styleLine(line.substring(index))}
                        </>
                    );
                }
            }
        }
        else {
            let index1 = line.indexOf('"');
            if (index1 === -1) continue;
            let index2 = line.indexOf('"', index1 + 1);
            if (index2 === -1) continue;
            return (
                <>
                    {styleLine(line.substring(0, index1))}
                    <span className={key}>{line.substring(index1, index2 + 1)}</span>
                    {styleLine(line.substring(index2 + 1))}
                </>
            );
        }
    }
    return <>{line}</>;
}

function multLine(lines: string[], activeIndex: number): JSX.Element {
    return (
        <>{lines.map((line, index) => (
            <li key={index} className={index === activeIndex ? 'active' : ''}>{styleLine(line)}</li>
        ))}</>
    );
}

export default Error;